import Vue from 'vue'
import Router from 'vue-router'
//import Hello from '@/components/Hello'

const Home = resolve => {
  require.ensure(['components/Home.vue'], () => {
    resolve(require('components/Home.vue'));
  }, 'Home');
};


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/index.html',
      name: 'Home',
      component: Home
    }
  ]
})



